
pre {
    font-family: "Montserrat", sans-serif;
    font-weight: 700;
    text-transform: capitalize;
    display: flex;
    justify-content: center;
    align-items: center;
}
.profile-container {
    width: 70%;
    display: flex;
    margin: auto;
    justify-content: center;
    align-items: center;
    flex-direction: column;
   
}

.tabpanel {
    display: flex;
    justify-content: center;
    align-items: center;
}

.profile-pic {
    position: relative;
    border-radius: 1000px;
    border: 1px solid rgba(250, 3, 147, 0.719);
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 5px;
    box-shadow: 0px 1px 15px -1px rgb(255, 0, 128);
    margin: 10px 0;
}

.info-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2em;
    flex-wrap: wrap;
    flex-direction: column;
}

.flex-col {
    flex-direction: column;
}
.profile-pic img {
    border-radius: 1000px;
}
@media (max-width: 1024px) {
    .profile-container {
        width: 97%;
    }
}
