.container {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: auto;
    padding: 100px 0;
}

.post-wrapper {
    width: 90%;
    min-width: 250px;
    box-shadow: 0 0 15px -2px;
    border-radius: 3px;
    font-family: "Montserrat", sans-serif;
    margin: 10px 0;
}

.post-header {
    display: flex;
    padding: 10px 15px;
    align-items: center;
    /* border-bottom: 1px solid rgba(194, 187, 187, 0.493); */
    box-shadow: 0 2px 5px -4px black;
}
.post-header > * {
    margin: 0 8px;
    font-family: "Montserrat", sans-serif;
    font-weight: 700;
    text-transform: capitalize;
}

.post-header-pp {
    padding: 2px;
    border: 1px solid rgba(0, 195, 255, 0.582);
    border-radius: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 0 0 15px -2px rgb(0, 174, 255);
}

.post-header span img {
    width: 30px;
    border-radius: 100px;
}
.post-body {
    padding: 20px 10px;
}

.post-body h4 {
    text-align: center;
    font-weight: bolder;
    color: rgb(0, 25, 94);
    padding-bottom: 10px;
}


.post-footer {
    padding: 10px 15px;
    border-top: 1px solid rgba(194, 187, 187, 0.493);
}

.unlike-btn {
    color: rgb(138, 133, 133);
    cursor: pointer;
}
.like-btn {
    color: rgb(255, 0, 106);
    cursor: pointer;
}


.posts-loading-wrapper{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin-bottom: 50px;
}

.posts-loading-wrapper div{
    
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    
}
.posts-loading-wrapper div > *{
    margin-right: 10px;
}

.mb{
    margin-bottom: 10px;
}